<template>
  <v-dialog max-width="600px" v-model="newLocation">
    <v-btn slot="activator" class="info" fab fixed bottom  right>
      <v-icon>add</v-icon>
    </v-btn>
    
    <v-card>
      
      <v-card-title>
        <h2 class="px-3 pt-3">Add a new location</h2>
      </v-card-title>
      <v-card-text>
        <v-form class="px-3" ref="form">

          <v-container fluid class="pa-0">
            <v-layout row wrap>

              <v-flex xs12 md6>
                <v-text-field label="Title" v-model="title" :rules="inputRules" prepend-inner-icon="restaurant"></v-text-field>
              </v-flex>
              <v-flex x12 md6>
                <v-text-field label="Location" v-model="location" :rules="inputRules" prepend-inner-icon="place"></v-text-field>
              </v-flex>

            </v-layout>
            <v-layout row wrap>

              <v-flex xs12 md6>
                <v-select label="Type" v-model="select" :items="types" :hint="`tag: ${select.key}`" item-text="name" item-value="key" offset-y persistent-hint return-object single-line  prepend-inner-icon="fastfood"></v-select>
              </v-flex>
              <v-flex x12 md6>
                <v-text-field label="Price" v-model="price" prepend-inner-icon="euro_symbol"></v-text-field>
              </v-flex>

            </v-layout>
            <v-layout>
              
              <v-flex>
                <v-text-field label="Url" v-model="url" :rules="inputRules" prepend-inner-icon="link"></v-text-field>
              </v-flex>

            </v-layout>
            <v-layout row wrap>

              <v-flex>
                <v-textarea label="Information" v-model="content" :rules="inputRules" prepend-inner-icon="edit"></v-textarea>
              </v-flex>

            </v-layout>
            <v-layout>
              <v-btn flat class="info my-3" @click="submit" :loading="loading">Add location</v-btn>
            </v-layout>
          </v-container>
        </v-form>
      </v-card-text>

    </v-card>

  </v-dialog>
</template>
<script>
import db from '@/fb'

export default {
  data() {
    return {
      title: '',
      location: '',
      price: 'undefined',
      url: '',
      content: '',
      currentUser: 'Duncan Heffron',
      select: { key: 'select-type', name: 'Select type' },
      types: [
        { key: 'all-you-can-eat', name: 'All you can eat' },
        { key: 'sushi', name: 'Sushi' },
        { key: 'fastfood', name: 'Fastfood' },
        { key: 'barbeque', name: 'Barbeque' },
        { key: 'chinees', name: 'Chinees' },
        { key: 'grieks', name: 'Grieks' },
        { key: 'italiaans', name: 'Italiaans' }
      ],
      inputRules: [
        v => v.length >= 3 || 'Minumum lenght is 3 characters'
      ],
      loading: false,
      newLocation: false
    }
  },
  methods: {
    submit() {
      if ( this.$refs.form.validate() ) {
        this.loading = true;
        
        const location = {
          title: this.title,
          price: this.price,
          url: this.url,
          location: this.location,
          type: this.select.key,
          content: this.content,
          author: this.currentUser
        }

        db.collection('locations').add(location).then(() => {
          this.loading = false;
          this.newLocation = false;
          this.$notify({
            group: 'foo',
            position: 'bottom right',
            type: 'success',
            title: 'Success',
            text: 'A new location was added to our database.'
          });
        })
      } 
    }
  }
}
</script>
