<template>
  <div class="dashboard">

    <v-container class="my-5">

      <v-layout row class="mb-3">

        <v-tooltip top>
          <v-btn small flat color="grey" @click="sortBy('title')" slot="activator">
            <v-icon left small>title</v-icon>
            <span class="captions text-lowercase">Title</span>
          </v-btn>
          <span>Sort locations by title</span>
        </v-tooltip>

        <v-tooltip top>
           <v-btn small flat color="grey" @click="sortBy('location')" slot="activator">
            <v-icon left small>place</v-icon>
            <span class="captions text-lowercase">Location</span>
          </v-btn>
          <span>Sort locations by location</span>
        </v-tooltip>

        <v-tooltip top>
          <v-btn small flat color="grey" @click="sortBy('type')" slot="activator">
            <v-icon left small>fastfood</v-icon>
            <span class="captions text-lowercase">Type</span>
          </v-btn>
          <span>Sort locations by type</span>
        </v-tooltip>

      </v-layout>
      
      <v-card flat v-for="location in locations" :key="location.id">
        <v-layout row wrap :class="`pa-3 location ${ location.type }`">
          <v-flex xs12 md6>
            <div class="caption grey--text">Restaurant</div>
            <div>{{ location.title }}</div>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <div class="caption grey--text">Price</div>
            <div>{{ location.price }} <span class="caption grey--text">p.p.</span></div>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <div class="caption grey--text">Location</div>
            <div>{{ location.location }}</div>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <div class="right">
              <v-chip small :class="`${ location.type } white--text caption my-2`">{{ location.type }}</v-chip>
            </div>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
      </v-card>

    </v-container>
  </div>
</template>

<script>
import db from '@/fb'

export default {
  data() {
    return {
      locations: []
    }
  },
  created() {
    db.collection('locations').get().then( querySnapshot => {
      querySnapshot.forEach( doc => {
        const data = {
          'id'      : doc.id,
          'title'   : doc.data().title,
          'price'   : doc.data().price,
          'url'     : doc.data().url,
          'location': doc.data().location,
          'type'    : doc.data().type,
          'content' : doc.data().content,
          'author'  : doc.data().author
        }
        this.locations.push( data )
      })
    })
  },
  methods: {
    sortBy(prop) {
      this.locations.sort( (a,b) => a[prop] < b[prop] ? -1 : 1 )
    }
  }
}
</script>

